import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ApiUserService } from '../api-user.service';
import { User } from '../user';

@Component({
  selector: 'app-add-user',
  templateUrl: './add-user.component.html',
  styleUrls: ['./add-user.component.css']
})
export class AddUserComponent implements OnInit {

userDetail : User = new User();
id = 0;
  constructor(private _api:ApiUserService, private _route:Router, private _activatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
  this.id = this._activatedRoute.snapshot.params.id;
    if(this.id==undefined){
      this.id = 0;
    }
    else{
      let ob = this._api.getUserById(this.id);
      ob.subscribe((res:any)=>{this.userDetail = res;})
    }
  }

  addEditUser(){
    if(this.id===0){
     let ob = this._api.insert(this.userDetail);
     ob.subscribe(()=>{this._route.navigate(['user']);})
    }
    else{
     let ob = this._api.update(this.userDetail,this.id);
     ob.subscribe(()=>{this._route.navigate(['user']);})
    }
  }
}