import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuard } from './auth.guard';
import { AddFacultyComponent } from './add-faculty/add-faculty.component';
import { DetailFacultyComponent } from './detail-faculty/detail-faculty.component';
import { FacultyComponent } from './faculty/faculty.component';
import { HomeComponent } from './home/home.component';
import { LoginComponent } from './login/login.component';
import { AddUserComponent } from './add-user/add-user.component';
import { DetailUserComponent } from './detail-user/detail-user.component';
import { UserComponent } from './user/user.component';

const routes: Routes = [
{path:"", component: HomeComponent},
{path:"login", component: LoginComponent},
{path:"faculty", component: FacultyComponent, canActivate:[AuthGuard]},
{path:"faculty/add", component: AddFacultyComponent, canActivate:[AuthGuard]},
{path:"faculty/edit/:id", component: AddFacultyComponent},
{path:"faculty/:id", component: DetailFacultyComponent},
{path:"user", component: UserComponent, canActivate:[AuthGuard]},
{path:"user/add", component: AddUserComponent, canActivate:[AuthGuard]},
{path:"user/edit/:id", component: AddUserComponent},
{path:"user/:id", component: DetailUserComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
