import { templateJitUrl } from '@angular/compiler';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiUserService } from '../api-user.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  UserName : string='';
  Password : string='';

  constructor(private _apiUser : ApiUserService, private _router: Router) { }

  ngOnInit(): void {
  }

  checkLogin(){
    this._apiUser.getAllUsers().subscribe((res:any)=>{
      res.records.forEach((temp:{UserName:string, Password:string}) => {
        if(temp.UserName==this.UserName && temp.Password==this.Password){
          sessionStorage.setItem('un',this.UserName);
          this._router.navigate(['faculty']);
        }
      });
      this.UserName = '';
      this.Password = '';
    });
  }

}